.page-header {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-top: 35px;
  padding-bottom: 35px;
  margin: 0 auto;

  @include vp-1023 {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  @include vp-767 {
    padding-top: 20px;
    padding-bottom: 20px;

    &--nojs {
      position: relative;
    }
  }
}

.page-header__overlay {
  @include vp-767 {
    position: fixed;
    display: none;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $color-tangaroa-opacity-05;
    z-index: 50;
    cursor: pointer;
  }
}
